  /* background by SVGBackgrounds.com */

.hero-container {
  /* background: url('../../img-home.jpg') center center/cover no-repeat; */
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.3);
  object-fit: contain;
}

.hero-container > h1 {
  color: #fff;
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
  margin-top: -100px;
  color: rgb(16, 194, 194);
  font-size: 80px;
}


.hero-container > p {
  margin-top: 8px;
  color: #fff;
  font-size: 60px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif
}

.hero-btns {
  margin-top: 32px;
}

.hero-btns .btrn {
  margin: 6px;
}

.fa-arrow-right {
  margin-left: 6px
}

@media screen and (max-width: 960px){
  .hero-container > h1 {
    font-size: 50px;
    margin-top: -150px
  }

  .hero-container > p {
    font-size: 35px;
  }
}

@media screen and (max-width: 768px){
  .hero-container > h1 {
    font-size: 50px;
    margin-top: -100px
  }

  .hero-container > p {
    font-size: 30px;
  }


  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 100%;
  }
}